<template>
  <div class="appSide_box">
    <img src="../assets/img/appSide/index_logo.png" alt="">
    <div class="click_btn_box tc">
      <el-button v-loading="isLoading" :disabled="isLoading" @click="changeBtn" class="click_btn" type="primary">
        <span v-show="!isLoading">一键生成</span>
      </el-button>
    </div>
    <div>
      <el-menu class="nav_menu_item" :default-active="current" :router="true" :unique-opened="true">
        <el-menu-item v-for="(item,index) in appSideList" :key="item.id" :index="item.url">
          <i :class="item.icon"></i>
          <span>{{item.name}}</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div class="nav_footer">
      <div class="nav_footer_logout" @click="exitOut">
        <i class="el-icon-switch-button"></i>
        <span>退出</span>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "appSide",
        data() {
            return {
                isLoading: false,
                appSideList: [
                    {
                        url: '/index/dataSettings',
                        name: '数据导入',
                        icon: 'el-icon-sell',
                        id: '2',
                    },
                    {
                        url: '/index/paramSettings',
                        name: '系统设置',
                        icon: 'el-icon-s-operation',
                        id: '1',
                    },
                    {
                        url: '/index/areaSettings',
                        name: '退频区域确定',
                        icon: 'el-icon-cpu',
                        id: '3',
                    },
                    {
                        url: '/index/CapacityToPredict',
                        name: '容量预测',
                        icon: 'el-icon-data-line',
                        id: '4',
                    },
                    {
                        url: '/index/frequencyShiftScheme',
                        name: '总退频方案输出',
                        icon: 'el-icon-document',
                        id: '5',
                    },
                    // {
                    //     url: '/index/SetUpThe',
                    //     name: '设置',
                    //     icon: 'el-icon-setting',
                    //     id: '6',
                    // },
                ]
            }
        },
        methods: {
            exitOut() {
                let _t = this;
                _t.$router.push({name: 'Login'});
            },
            // 点击一键生成
            changeBtn() {
                let _t = this;
                _t.isLoading = true;
                _t.get();
            },
            // get
            get() {
                var _t = this;
                _t.$api.post('/region/get1', {}, function (val1) {
                    if (val1) {
                        _t.sectorBuild();
                    } else {
                        _t.isLoading = false;
                        _t.$message.error('退频区域确定数据生成错误!');
                    }
                });
            },
            sectorBuild() {
                var _t = this;
                _t.$api.post('/sector/build', {}, function (val7) {
                    if (val7) {
                        _t.frequencyShiftBuild();
                    } else {
                        _t.isLoading = false;
                        _t.$message.error('扇区-小区关系整理错误!');
                    }
                });
            },
            frequencyShiftBuild() {
                var _t = this;
                _t.$api.post('/frequencyshift/build', {}, function (val8) {
                    if (val8) {
                        _t.insert();
                    } else {
                        _t.isLoading = false;
                        _t.$message.error('初步退频方案输出错误!');
                    }
                });
            },
            insert() {
                var _t = this;
                _t.$api.post('/forecast/insert', {}, function (val2) {
                    if (val2) {
                        _t.capacity();
                    } else {
                        _t.isLoading = false;
                        _t.$message.error('小区容量预测数据生成错误!');
                    }
                });
            },
            capacity() {
                var _t = this;
                _t.$api.post('/forecast/capacity', {}, function (val3) {
                    if (val3) {
                        _t.saveSectorExpansionPlanList();
                    } else {
                        _t.isLoading = false;
                        _t.$message.error('小区分担流量数据生成错误!');
                    }
                });
            },
            saveSectorExpansionPlanList() {
                var _t = this;
                _t.$api.post('/expansion/get', {}, function (val4) {
                    if (val4) {
                        _t.saveTotalFrequencySchemeOutputForm();
                    } else {
                        _t.isLoading = false;
                        _t.$message.error('扇区扩容方案生成错误!');
                    }
                });
            },
            saveTotalFrequencySchemeOutputForm() {
                var _t = this;
                _t.$api.post('/expansion/getAll', {}, function (val5) {
                    console.log(val5)
                    if (val5) {
                        _t.isLoading = false;
                        _t.$alert('总退频方案已生成!', '温馨提示');
                    } else {
                        _t.isLoading = false;
                        _t.$message.error(' 总退频方案生成错误!');
                    }
                });
            }
        },
        computed: {
            // 监听 url 详情页面的导航
            current() {
                var arr = this.$route.path.split('/');
                if (arr.length > 4) {
                    arr.pop();
                    return arr.join("/");
                } else {
                    return this.$route.path;
                }
            }
        },
    }
</script>

<style scoped>
  .appSide_box {
    width: 220px;
    background-color: #1e2d40;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
  }

  .click_btn_box {
    padding: 30px 0;
  }

  .click_btn {
    width: 180px;
    border-radius: 20px;
  }

  .nav_menu_item {
    background-color: transparent;
  }

  .nav_footer {
    position: fixed;
    width: 220px;
    height: 50px;
    left: 0;
    bottom: 0;
    text-align: center;
    background-color: #0e2038;
  }

  .nav_footer_logout {
    line-height: 50px;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
  }

  .nav_menu_item .el-menu-item.is-active {
    background-color: #fff;
  }
</style>
<style>
  .nav_menu_item li {
    color: #fff;
  }

  .nav_menu_item .el-menu-item:focus,
  .nav_menu_item .el-menu-item:hover {
    color: #1e2d40;
  }
</style>



