<template>
  <div class="login_box">
    <div class="login_form_box">
      <img class="widthAll" src="../assets/img/login/login_form_bg.png" alt="">
      <!--右侧表单区域-->
      <div class="login_form">
        <p class="tc formTitle">面向5G的D频段退频工具</p>
        <el-form :model="formItem">
          <el-form-item>
            <el-input class="login_input" v-model="formItem.username" placeholder="请输入用户名"/>
          </el-form-item>
          <el-form-item>
            <el-input
              class="login_input"
              type="password"
              v-model="formItem.password"
              @keyup.enter.native="clickLogin($event)"
              placeholder="请输入密码"/>
          </el-form-item>
          <el-form-item>
            <el-button
              class="loginBtn"
              @click="clickLogin" @onkeydown="clickLogin">马上登录
            </el-button>
          </el-form-item>
          <div v-if="formItem.errorFlag" class="login_error_tip">
            {{formItem.errorTip}}
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                formItem: {
                    username: '', // 账户名
                    password: '', // 密码
                    errorFlag: true, // 错误标识
                    errorTip: '', // 错误提示
                }
            }
        },
        methods: {
            // 点击登录按钮
            clickLogin(event) {
                event.preventDefault();
                var _t = this;
                // 校验不能为空
                if (_t.formItem.username.trim() === '' || _t.formItem.password.trim() === '') {
                    _t.formItem.errorFlag = true;
                    _t.formItem.errorTip = '用户名或密码不能为空!';
                } else {
                    _t.formItem.errorFlag = false;
                    _t.formItem.errorTip = '';
                }
                // 可以提交
                if (_t.formItem.errorFlag === false) {
                    _t.$api.post('/user/login', {
                        userName: _t.formItem.username.trim(),
                        userPwd: _t.formItem.password.trim(),
                    }, function (res) {
                        if (res === true) {
                            _t.$router.push({name: 'Index'});
                        } else {
                            _t.formItem.errorTip = '用户名或密码不正确!';
                            _t.formItem.errorFlag = true;
                        }
                    });
                }
            }
        },
    }
</script>

<style scoped>
  .login_box {
    width: 100%;
    height: 100%;
    background: url("../assets/img/login/login_bg.jpg") no-repeat;
    background-size: 100% 100%;
    position: relative;
  }

  .login_form_box {
    width: 1000px;
    height: 470px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .login_form {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    padding: 50px 80px;
  }

  .formTitle {
    font-size: 24px;
    font-weight: bold;
    color: #0185cf;
    margin-bottom: 50px;
  }

  .loginBtn {
    width: 100%;
    height: 60px;
    line-height: 60px;
    padding: 0;
    background: url("../assets/img/login/login_btn.png") no-repeat;
    color: #fff !important;
    font-size: 18px;
    outline: none;
    border: none;
  }

  .loginBtn:focus,
  .loginBtn:hover {
    color: #fff !important;
    outline: none;
    border: none;
  }

  .login_error_tip {
    font-size: 12px;
    color: #fb6041;
  }
</style>
<style>
  .login_input .el-input__inner {
    height: 60px !important;
    line-height: 60px !important;
  }
</style>



