<template>
  <div class="CapacityToPredict">
    <div class="predictionBox">
      <p class="titleText">
        <span class="lineBg"></span>
        <span>小区容量预测数据表</span>
      </p>
      <el-table :data="predictionData" border height="600">
        <el-table-column prop="datatime" label="日期" header-align="center" align="center"/>
        <el-table-column prop="enbid" label="Enbid" header-align="center" align="center"/>
        <el-table-column prop="cgi" label="Cellid" header-align="center" align="center"/>
        <el-table-column prop="avgErab" label="平均E-RAB流量(KB)小区自忙时" header-align="center" align="center"/>
        <el-table-column prop="numRpc" label="有数据传输的RRC数" header-align="center" align="center"/>
        <el-table-column prop="ratePusch" label="上行利用率PUSCH" header-align="center" align="center"/>
        <el-table-column prop="ratePdsch" label="下行利用率PDSCH" header-align="center" align="center"/>
        <el-table-column prop="ratePdcch" label="下行利用率PDCCH" header-align="center" align="center"/>
        <el-table-column prop="upData" label="最大上行流量(GB)" header-align="center" align="center"/>
        <el-table-column prop="downData" label="最大下行流量(GB)" header-align="center" align="center"/>
      </el-table>
    </div>
    <hr/>
    <!--<div class="trafficDataTableBox">
      <p class="titleText">
        <span class="lineBg"></span>
        <span>小区分担流量数据表</span>
      </p>
      <el-table :data="trafficDataTable" border>
        <el-table-column prop="enodebName" label="基站ID" header-align="center" align="center"/>
        <el-table-column prop="sector" label="扇区ID" header-align="center" align="center"/>
        <el-table-column label="源小区" header-align="center" align="center">
          <el-table-column prop="enbid1" label="Enbid" header-align="center" align="center"/>
          <el-table-column prop="cgi1" label="Cellid" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand1" label="频点" header-align="center" align="center"/>
          <el-table-column prop="carrier1" label="等效载波数" header-align="center" align="center"/>
        </el-table-column>
        <el-table-column label="分担小区" header-align="center" align="center">
          <el-table-column prop="enbid2" label="Enbid" header-align="center" align="center"/>
          <el-table-column prop="cgi2" label="Cellid" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand2" label="频点" header-align="center" align="center"/>
          <el-table-column prop="carrier2" label="分担等效载波数" header-align="center" align="center"/>
        </el-table-column>
      </el-table>
    </div>
    <hr/>-->
    <div class="expansionPlanListBox">
      <p class="titleText">
        <span class="lineBg"></span>
        <span>扇区扩容方案列表</span>
      </p>
      <el-table :data="expansionPlanList" border>
        <el-table-column label="工参数据" header-align="center" align="center">
          <el-table-column prop="enodebName" label="基站ID" header-align="center" align="center"/>
          <el-table-column prop="cellName" label="基站名" header-align="center" align="center"/>
          <el-table-column prop="sector" label="扇区ID" header-align="center" align="center"/>
          <el-table-column prop="coverageType" label="基站类型" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand" label="原扇区结构" header-align="center" align="center"/>
          <el-table-column prop="newWorkFrequencyBand" label="退频后扇区结构" header-align="center" align="center"/>
        </el-table-column>
        <el-table-column label="载波计算" header-align="center" align="center">
          <el-table-column prop="carrier" label="存量载波" header-align="center" align="center"/>
          <el-table-column prop="needCarrier" label="载波需求" header-align="center" align="center"/>
          <el-table-column prop="gap" label="载波GAP" header-align="center" align="center"/>
        </el-table-column>
        <!--<el-table-column label="扩容方案">
          <el-table-column prop="expansionPlan" label="扩容方案" header-align="center" align="center"/>
          <el-table-column prop="expansionWorkFrequencyBand" label="扩容频点" header-align="center" align="center"/>
        </el-table-column>-->
        <!--<el-table-column label="方案选择">
          <el-table-column type="selection">

          </el-table-column>
        </el-table-column>-->
      </el-table>
    </div>
  </div>
</template>

<script>
    export default {
        name: "CapacityToPredict",
        data() {
            return {
                //小区容量预测数据表数据
                predictionData: [],
                //小区分担流量数据表数据
                trafficDataTable: [],
                //扇区扩容方案列表数据
                expansionPlanList: [],
            }
        },
        methods: {
            //1 predictionData
            getData() {
                let _t = this;
                _t.$api.post('/forecast/get', {}, function (res) {
                    if (res) {
                        _t.predictionData = res;
                    }
                })
            },
            //分担流量
            getDataTwo() {
                let _t = this;
                _t.$api.post('/forecast/getSmallDifferenceFlowForms', {}, function (res) {
                    if (res) {
                        _t.trafficDataTable = res;
                    }
                })
            },
            //扇区扩容方案列表数据
            getDataThree() {
                let _t = this;
                _t.$api.post('/forecast/getSectorExpansionPlanForm', {}, function (res) {
                    if (res) {
                        _t.expansionPlanList = res;
                    }
                })
            },
        },
        created() {
            let _t = this;
            //1
            _t.getData();
            //分担流量
            _t.getDataTwo();
            //扇区扩容方案列表数据
            _t.getDataThree();
        }
    }
</script>

<style scoped>

</style>



