<template>
  <div class="dataSettings_box">
    <el-tabs v-model="activeName" type="border-card" @tab-click="get_pane">
      <el-tab-pane label="小区工参" name="one">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>小区工参导入</span>
        </p>
        <div class="marginBottom20">
          <el-upload
            class="displayInline"
            :action="this.$api.root() + '/data/uploadLteInfo'"
            :data="dataCate"
            name="file"
            :on-error="errorOne"
            :on-success="handleAvatarSuccess">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label="true">追加导入</el-radio>
            <el-radio :label="false">覆盖导入</el-radio>
          </el-radio-group>
          <el-button @click="getData" type="primary" class="marginLeft50">查询</el-button>
        </div>
        <hr>
        <div class="wokeDataTable">
          <el-table :data="wokeData" border height="700">
            <el-table-column prop="cellName" label="物理站点名称" header-align="center" align="center"/>
            <el-table-column prop="longitude" label="站点经度" header-align="center" align="center"/>
            <el-table-column prop="latitude" label="站点纬度" header-align="center" align="center"/>
            <el-table-column prop="coverageType" label="站型" header-align="center" align="center"/>
            <el-table-column prop="antennaHeight" label="站高" header-align="center" align="center"/>
            <el-table-column prop="azimuth" label="方向角" header-align="center" align="center"/>
            <el-table-column prop="totalDownTiltAngle" label="下倾角" header-align="center" align="center"/>
            <el-table-column prop="workFrequencyBand" label="频点" header-align="center" align="center"/>
            <el-table-column prop="vendor" label="设备厂家" header-align="center" align="center"/>
            <el-table-column prop="supportCarriers" label="载波配置" header-align="center" align="center"/>
            <el-table-column prop="coverScene" label="覆盖场景" header-align="center" align="center"/>
          </el-table>
        </div>
      </el-tab-pane>
      <!--5G-->
      <el-tab-pane label="NR数据" name="seven">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>NR数据导入</span>
        </p>
        <div class="marginBottom20">
          <el-upload
            class="displayInline"
            :action="this.$api.root() + '/data/uploadNrInfo'"
            :on-error="errorTwo"
            :on-success="handleAvatarSuccess5G">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label=true>追加导入</el-radio>
            <el-radio :label=false>覆盖导入</el-radio>
          </el-radio-group>
          <el-button type="primary" class="marginLeft50" @click="getData5g">查询</el-button>
        </div>
        <hr>
        <el-table :data="fiveData" border height="700">
          <el-table-column prop="cellName" label="小区中文名" header-align="center" align="center"/>
          <el-table-column prop="longitude" label="经度" header-align="center" align="center"/>
          <el-table-column prop="latitude" label="纬度" header-align="center" align="center"/>
          <el-table-column prop="coverageType" label="覆盖类型" header-align="center" align="center"/>
          <el-table-column prop="antennaHeight" label="天线挂高" header-align="center" align="center"/>
          <el-table-column prop="azimuth" label=" 方位角" header-align="center" align="center"/>
          <el-table-column prop="totalDownTiltAngle" label="总下倾角" header-align="center" align="center"/>
          <el-table-column prop="workFrequencyBand" label="工作频段" header-align="center" align="center"/>
          <el-table-column prop="vendor" label="厂家名称" header-align="center" align="center"/>
          <el-table-column prop="coverScene" label="覆盖场景" header-align="center" align="center"/>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="话统数据" name="two">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>话统数据导入</span>
        </p>
        <div class="marginBottom20">
          <el-upload
            class="displayInline"
            :action="this.$api.root() + '/data/uploadTalkInfo'"
            :on-error="errorThree"
            :on-success="handleAvatarSuccessHt">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label=true>追加导入</el-radio>
            <el-radio :label=false>覆盖导入</el-radio>
          </el-radio-group>
          <el-button type="primary" class="marginLeft50" @click="getDataHt">查询</el-button>
        </div>
        <hr>
        <el-table :data="systemData" border height="700">
          <el-table-column prop="datetime" label="日期" header-align="center" align="center"/>
          <el-table-column prop="enbid" label="enbid" header-align="center" align="center"/>
          <el-table-column prop="erab" label="小区自忙时平均E-RAB流量(KB)" header-align="center" align="center"/>
          <el-table-column prop="rrc" label="有数据传输的RRC数" header-align="center" align="center"/>
          <el-table-column prop="pusch" label="上行利用率PUSCH(%)" header-align="center" align="center"/>
          <el-table-column prop="pdsch" label="下行利用率PDSCH(%)" header-align="center" align="center"/>
          <el-table-column prop="pdcch" label="下行利用率PDCCH(%)" header-align="center" align="center"/>
          <el-table-column prop="upStream" label="上行流量(GB)" header-align="center" align="center"/>
          <el-table-column prop="downStream" label="下行流量(GB)" header-align="center" align="center"/>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="邻区关系表" name="three">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>邻区关系表数据导入</span>
        </p>
        <div class="marginBottom20">
          <el-upload
            class="displayInline"
            :action="this.$api.root() + '/data/uploadNeighborRelationshipInfo'"
            :on-error="errorFour"
            :on-success="handleAvatarSuccessLqg">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label=true>追加导入</el-radio>
            <el-radio :label=false>覆盖导入</el-radio>
          </el-radio-group>
          <el-button type="primary" class="marginLeft50" @click="getDataLqg">查询</el-button>
        </div>
        <hr>
        <el-table :data="regionTableData" border height="700">
          <el-table-column prop="enbid" label="Enb id" header-align="center" align="center"/>
          <el-table-column prop="cellId" label="Cell id" header-align="center" align="center"/>
          <el-table-column prop="nenbId" label="N enbid" header-align="center" align="center"/>
          <el-table-column prop="nCellId" label="N cellid" header-align="center" align="center"/>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="邻区统计数据" name="four">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>邻区统计数据导入</span>
        </p>
        <div class="marginBottom20">
          <el-upload
            class="displayInline"
            :action="this.$api.root()+'/data/uploadNeighborStatisticsInfo'"
            :on-error="errorFive"
            :on-success="handleAvatarSuccessLqt">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label=true>追加导入</el-radio>
            <el-radio :label=false>覆盖导入</el-radio>
          </el-radio-group>
          <el-button type="primary" class="marginLeft50" @click="getDataLqt">查询</el-button>
        </div>
        <hr>
        <el-table :data="statisTableData" border height="700">
          <el-table-column prop="enbid" label="Enb id" header-align="center" align="center"/>
          <el-table-column prop="cellId" label="Cell id" header-align="center" align="center"/>
          <el-table-column prop="nenbId" label="N enbid" header-align="center" align="center"/>
          <el-table-column prop="nCellId" label="N cellid" header-align="center" align="center"/>
          <el-table-column prop="switchApplicationTimes" label="切换申请次数" header-align="center" align="center"/>
          <el-table-column prop="switchSuccessfulTimes" label="切换成功次数" header-align="center" align="center"/>
        </el-table>
      </el-tab-pane>
      <!--<el-tab-pane label="D频段(D1/D2)小区配置参数" name="five">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>D频段(D1/D2)小区配置参数</span>
        </p>
        <div class="marginBottom20">
          <el-upload class="displayInline" action="">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label=true>追加导入</el-radio>
            <el-radio :label=false>覆盖导入</el-radio>
          </el-radio-group>
        </div>
        <hr>
        <el-table :data="frequencyTableData" border></el-table>
      </el-tab-pane>-->
      <el-tab-pane label="终端支持受限基站退频清单" name="six">
        <p class="titleText">
          <span class="lineBg"></span>
          <span>终端支持受限基站退频清单导入</span>
        </p>
        <div class="marginBottom20">
          <el-upload class="displayInline" :action="this.$api.root() + '/data/uploadTerminalInfo'"
                     :on-success="handleAvatarSuccessZd">
            <el-button type="primary" @click="getUpDate">浏览</el-button>
          </el-upload>
          <el-radio-group class="marginLeft50" v-model="workForm.radio">
            <el-radio :label=true>追加导入</el-radio>
            <el-radio :label=false>覆盖导入</el-radio>
          </el-radio-group>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
    export default {
        name: "dataSettings",
        data() {
            return {
                // 标签页的默认选中
                activeName: 'one',
                // 小区工参
                workForm: {
                    radio: true,
                },
                wokeData: [],
                // 话筒数据
                /*systemForm: {
                    radio: true,
                },*/
                systemData: [],
                // 邻区关系表
                /*regionForm: {
                    radio: true,
                },*/
                regionTableData: [],
                // 邻区统计表
                /*statisForm: {
                    radio: true,
                },*/
                statisTableData: [],
                // D频段
                /*frequencyForm: {
                    radio: true,
                },*/
                frequencyTableData: [],
                // 终端
                /*terminalForm: {
                    radio: true,
                },*/
                //5g
                fiveData: [],
                dataCate: {
                    truncate: true
                }
            }
        },
        methods: {
            // 小区工参失败
            errorOne(err, file, fileList) {
                var _t = this;
                _t.$message.error('上传失败!');
            },
            //小区工参上传成功后执行
            handleAvatarSuccess(val, file, fileList) {
                let _t = this;
                _t.$message.success('上传成功!');
                _t.getData();
            },
            // NR上传失败
            errorTwo(err, file, fileList) {
                var _t = this;
                _t.$message.error('上传失败!');
            },
            // 话筒失败
            errorThree(err, file, fileList) {
                var _t = this;
                _t.$message.error('上传失败!');
            },
            // 邻区关系表失败
            errorFour(err, file, fileList) {
                var _t = this;
                _t.$message.error('上传失败!');
            },
            // 邻区统计表上传失败
            errorFive(err, file, fileList) {
                var _t = this;
                _t.$message.error('上传失败!');
            },
            get_pane() {
                let _t = this;
                _t.workForm.radio = true;
            },
            getUpDate() {
                let _t = this;
                _t.dataCate.truncate = _t.workForm.radio;
            },
            //小区工参table数据获取
            getData() {
                let _t = this;
                _t.$api.post('/data/getLteInfo', {}, function (res) {
                    if (res) {
                        _t.wokeData = res;
                    }
                })
            },
            //话统数据上传成功后执行
            handleAvatarSuccessHt(val, file, fileList) {
                let _t = this;
                _t.$message.success('上传成功!');
                _t.getDataHt();
            },
            //话统数据Table获取
            getDataHt() {
                let _t = this;
                _t.$api.post('/data/getTalkInfo', {}, function (res) {
                    if (res) {
                        _t.systemData = res;
                    }
                })
            },
            //邻区关系表上传成功后执行
            handleAvatarSuccessLqg(val, file, fileList) {
                let _t = this;
                _t.$message.success('上传成功!');
                _t.getDataLqg();
            },
            //邻区关系表Table获取
            getDataLqg() {
                let _t = this;
                _t.$api.post('/data/getNeighborRelationshipInfo', {}, function (res) {
                    if (res) {
                        _t.regionTableData = res;
                    }
                })
            },
            //邻区统计表上传成功后执行
            handleAvatarSuccessLqt(val, file, fileList) {
                let _t = this;
                _t.$message.success('上传成功!');
                _t.getDataLqg();
            },
            //邻区统计表Table获取
            getDataLqt() {
                let _t = this;
                _t.$api.post('/data/getNeighborStatisticsInfo', {}, function (res) {
                    if (res) {
                        _t.statisTableData = res;
                    }
                })
            },
            // 终端上传成功后执行
            handleAvatarSuccessZd(val, file, fileList) {
                let _t = this;
            },
            // 5g上传
            handleAvatarSuccess5G(val, file, fileList) {
                let _t = this;
                _t.$message.success('上传成功!');
                _t.getData5g();
            },
            getData5g() {
                let _t = this;
                _t.$api.post('/data/getNrInfo', {}, function (res) {
                    if (res) {
                        _t.fiveData = res;
                    }
                })
            }
        },
        created() {
            let _t = this;
            //小区工参
            // _t.getData();
            //话统数据
            // _t.getDataHt();
            //邻区关系表
            // _t.getDataLqg();
            //邻区统计数据
            // _t.getDataLqt();
            /*//终端
            _t.getDataLZd();*/
            //5g
            // _t.getData5g();
        },

    }
</script>

<style scoped>

</style>



